/*  */
span.wpcf7-form-control-wrap .wpcf7-not-valid {
  border: 1px solid red;
  background-color: rgba(201, 72, 0, 0.20);
}

span.wpcf7-form-control-wrap .wpcf7-not-valid.form__privacy-text.form-checkbox {
  border: none;
}

