/*  */
.wpcf7-not-valid-tip {
  display: none;
  /*  */
}

.wpcf7 form .wpcf7-response-output {
    padding-bottom: rem(25);
    padding-right: rem(54);
    padding-left: rem(54);
    margin-top: 0;
    font-size: rem(14);
    font-family: $jp-family;
    font-weight: $regular;
    line-height: 1.5;
    color: $red;
    border: none;
    text-align: left;
    width: 100%;
    display: inline-block;
    // text-indent: rem(-7);
    @include mq("md") {
     text-align: center;
    }
}

    /* form__privacy */
span.wpcf7-form-control-wrap .wpcf7-not-valid {
  border: none;
}

/**/
// input[type="submit"] {
//   appearance: none;
//   -webkit-appearance: none;
//   -moz-appearance: none;
//   color: $white;
// }

/**/
input[type="submit"] {
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: var(--white);
  /* CSS */
  color: var(--green3);
  /*  */
  border: 1px solid var(--green3);
  /*  */
  transition: background-color .2s, color .2s;
  /*  */
}

/*  */
input[type="submit"]:hover {
  background-color: var(--green3);
  /*  */
  color: var(--white);
  /*  */
}

span.wpcf7-spinner {
  display: none;
}

.submit-btn-wrapper {
  text-align: center;
}